






















var page = 0,totalpage=0;
import mGroupbuyList from '@/components/m-groupbuy-list'
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
export default {
	data() {
		return {
			// 附近门店
			groupsellList:[],
			mloading:'more'
		}
	},
	components: {
		mGroupbuyList,
		uniLoadMore
	},
	methods:{
		getGroupsellList(){
			uni.showLoading({});
			if(totalpage&&page > totalpage){
				this.mloading='noMore';
				uni.hideLoading();
				uni.stopPullDownRefresh();
				return ;
			}
			this.mPost('/server/p/group/products',{
				start:page,
				length:20
			}).then(res=>{
					if(res.data){
						let data = res.data;
						if(data.list){
							totalpage=data.pages;
							var newsList = data.list;
							this.groupsellList = this.groupsellList.concat(newsList);
							uni.hideLoading();
							page++;
						}
					}
			}).catch(err=>{
				uni.hideLoading();
			});
		},
		//跳转到商家
		goStore(data){
			uni.navigateTo({
				url:"/pages/store/store?storeid="+data.storeid+"&typeid="+data.typeid
			})
		}
	},
	// 加载更多
	onReachBottom(){
		this.getGroupsellList();
	},
	//下拉刷新
	onPullDownRefresh : function(){
		// 重置分页及数据
		page = 1;
		this.groupsellList =[];
		this.getGroupsellList();
	},
	onLoad(){
		page = 1;
		groupsellList:[];
		this.getGroupsellList();
	}
}
